.postCard {
  margin-bottom: 20px;
}

img {
  height: 500px;
  width: 500px;
}

.body{
  background-color: rgba(0, 0, 255, 0.116);
  justify-content: center;
}

card {
  margin: 0 auto;
  margin-top: 7%; 
  float: none;
  margin-bottom: 10px;
  width: 300px;
  height: 400px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
  padding: 0 0 0 0;  
}

h2 {
  text-align:center;
  font-family:Helvetica;
  font-variant:small-caps;
  letter-spacing:2px;
  color:#ffff;
  background-color:#3399FF;
  padding-bottom: 5%;
  padding-top: 5%;
}

.container {
  position: relative;
  width: 300px;
  height: 400px;
}

.form-group {
  justify-content: center;
}
